input[type="checkbox"] {
  cursor: pointer;
}

.form-check-label {
  cursor: pointer;
}

.element-container {
  width: 80%;
  margin-left: 10%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.filter-element {
  display: inline-flex;
  width: 200px;
  height: 150px;
  margin: 5px;
}

.orange {
  background-color: #f38630;
}
.green {
  background-color: #6fb936;
}
.purple {
  background-color: #7d4696;
}
